import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AirportService } from 'src/app/shared/services/airport.service';

@Component({
  selector: 'airport-form',
  templateUrl: './airport-form.component.html',
  styleUrls: ['./airport-form.component.css']
})
export class AirportFormComponent implements OnInit {

  airports$;
  airport = {};
  id;
  submitted = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private airportService: AirportService) {
    this.airports$ = this.airportService.getAll();
    this.id = this.route.snapshot.paramMap.get('id');
    if (this.id) {
      this.airportService.get(this.id).subscribe(p => this.airport = p);
    }

  }

  save(product) {
    this.submitted = true;
    if (this.id) this.airportService.update(this.id, product);
    else this.airportService.create(product);
    this.submitted = false;
    this.airport = {};
    //this.router.navigate(['/admin/airports']);
  }

  delete() {
    if (!confirm('Are you sure you want to delete this entry?')) return;

    this.airportService.delete(this.id);
    this.router.navigate(['/admin/airports']);
  }
  viewList() {
    this.router.navigate(['/admin/airports']);
  }
  ngOnInit() {
  }

}
