import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/shared/services/auth.service';
import { DataService } from 'src/app/shared/services/data.service';
import { BookingService } from 'src/app/shared/services/booking.service';

@Component({
  selector: 'booking-summary',
  templateUrl: './booking-summary.component.html',
  styleUrls: ['./booking-summary.component.css']
})
export class BookingSummaryComponent implements OnInit, OnDestroy {

  schedule = {};
  customer = {};
  userSubscription: Subscription;
  userId: string;

  constructor(
    private router: Router,
    private authService: AuthService,
    private dataService: DataService,
    private bookingService: BookingService) {

  }

  ngOnInit() {
    this.userSubscription = this.authService.user$.subscribe(user => this.userId = user.uid);
    this.dataService.currentData.subscribe(schedule => {
      this.schedule = schedule;
      if (Object.keys(this.schedule).length === 0) return this.router.navigate(["flights"]);
    });
  }

  ngOnDestroy() {
    this.userSubscription.unsubscribe();
  }

  async save(customer) {

    const booking = {
      dateBooked: new Date().getTime(),
      customer,
      schedule: this.schedule,
      userId: this.userId
    };

    await this.bookingService.saveBooking(booking);
    this.router.navigate(["flights"])
  }
}
