import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  schedule = {};
  private defaultData = new BehaviorSubject(this.schedule);
  currentData = this.defaultData.asObservable();

  constructor() { }

  sendData(schedule) {
    this.defaultData.next(schedule)
  }

}
