import { ShoppingCart } from './shopping-cart';

export class Order {
    datePlaced: number;
    items: any[];

    constructor(public userId, public shipping: any, shoppingCart: ShoppingCart) {
        this.datePlaced = new Date().getDate();
        this.items = shoppingCart.items.map((i: any) => {
            return {
                product: {
                    title: i.title,
                    imageUrl: i.imageUrl,
                    price: i.price
                },
                quantity: i.quantity,
                totalPrice: i.totalPrice
            }
        })
    }
}