import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { DataTableModule } from 'angular5-data-table';
import { FormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { AdminProductsComponent } from './components/admin-products/admin-products.component';
import { AdminOrdersComponent } from './components/admin-orders/admin-orders.component';
import { ProductFormComponent } from './components/product-form/product-form.component';
import { AuthGuard } from '../shared/services/auth-guard.service';
import { AdminAuthGuard } from './services/admin-auth-guard.service';
import { ShoppingModule } from '../shopping/shopping.module';
import { AirportFormComponent } from './components/airport-form/airport-form.component';
import { FlightFormComponent } from './components/flight-form/flight-form.component';
import { FlightScheduleComponent } from './components/flight-schedule/flight-schedule.component';
import { AdminBookingsComponent } from './components/admin-bookings/admin-bookings.component';



@NgModule({
  declarations: [
    AdminProductsComponent,
    AdminOrdersComponent,
    ProductFormComponent,
    AirportFormComponent,
    FlightFormComponent,
    FlightScheduleComponent,
    AdminBookingsComponent
  ],
  imports: [
    CommonModule,
    ShoppingModule,
    SharedModule,
    FormsModule,
    DataTableModule.forRoot(),
    RouterModule.forChild([
      {
        path: 'admin/airports',
        component: AirportFormComponent,
        canActivate: [AuthGuard, AdminAuthGuard]
      },
      {
        path: 'admin/flights',
        component: FlightFormComponent,
        canActivate: [AuthGuard, AdminAuthGuard]
      },
      {
        path: 'admin/flight-schedule',
        component: FlightScheduleComponent,
        canActivate: [AuthGuard, AdminAuthGuard]
      },
      {
        path: 'admin/bookings',
        component: AdminBookingsComponent,
        canActivate: [AuthGuard, AdminAuthGuard]
      }
    ])
  ]
})
export class AdminModule { }
