import { Component, OnDestroy, OnInit } from '@angular/core';
import { FlightService } from 'src/app/shared/services/flight.service';
import { AirportService } from 'src/app/shared/services/airport.service';
import { Router, NavigationExtras } from '@angular/router';
import { Subscription } from 'rxjs';
import { DataService } from 'src/app/shared/services/data.service';

@Component({
  selector: 'flights',
  templateUrl: './flights.component.html',
  styleUrls: ['./flights.component.css']
})
export class FlightsComponent implements OnInit, OnDestroy {

  airports$;
  flight = {};
  schedules = [];
  filteredSchedules = [];
  subscription: Subscription;

  constructor(
    private router: Router,
    private airportService: AirportService,
    private flightService: FlightService,
    private dataService: DataService) {
    this.airports$ = this.airportService.getAll();
  }

  ngOnInit() {
    this.subscription = this.flightService.getSchedules().subscribe(p => {
      this.schedules = p;
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  find(search) {
    const departure_date = search.departure.year.toString() + '-' + search.departure.month.toString() + '-' + search.departure.day.toString();
    if (search.source && search.destination && departure_date) {
      this.filteredSchedules = this.schedules.filter(p =>
        (p.departure.city === search.source && p.arrival.city === search.destination && p.departure_date === departure_date))
    }
    else {
      this.filteredSchedules = [];
    }
  }

  book(schedule) {
    delete schedule.key;
    this.dataService.sendData(schedule);
    this.router.navigate(["flight-summary"]);
  }
}
