import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FlightService } from 'src/app/shared/services/flight.service';
import { AirlineService } from 'src/app/shared/services/airline.service';

@Component({
  selector: 'flight-form',
  templateUrl: './flight-form.component.html',
  styleUrls: ['./flight-form.component.css']
})
export class FlightFormComponent implements OnInit {

  flights$;
  airlines$;
  flight = {};
  id;
  submitted = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private airlineService: AirlineService,
    private flightService: FlightService) {
    this.airlines$ = this.airlineService.getAll();
    this.flights$ = this.flightService.getAll();
    this.id = this.route.snapshot.paramMap.get('id');
    if (this.id) {
      this.flightService.get(this.id).subscribe(p => this.flight = p);
    }

  }

  save(product) {
    this.submitted = true;
    if (this.id) this.flightService.update(this.id, product);
    else this.flightService.create(product);
    this.submitted = false;
    this.flight = {};
    //this.router.navigate(['/admin/flights']);
  }

  delete() {
    if (!confirm('Are you sure you want to delete this entry?')) return;

    this.flightService.delete(this.id);
    this.router.navigate(['/admin/flights']);
  }
  viewList() {
    this.router.navigate(['/admin/flights']);
  }
  ngOnInit() {
  }

}
